<template>
  <div>
    <Row>
      <Col>
        搜索：
        <Input placeholder style="width:200px;" v-model="obj.keyword"></Input>
        <Button
          type="info"
          style="border-radius:0;height:32px;margin-right:10px;"
          @click="getData"
        >搜索</Button>
        <span>( 提示：支持多关键词搜索，不同关键词中间用分号隔开 )</span>
      </Col>
    </Row>
    <br />
    <Row>
      <Col>
        更新时间：
        <DatePicker
          type="datetimerange"
          placeholder="Select date and time"
          style="width: 300px"
          v-model="datetime"
          @on-change="changeDate"
        ></DatePicker>
      </Col>
    </Row>
    <br />
    <Row>
      <Col span="4">纯/杂交</Col>
      <Col span="20" style="display:flex;">
        <Checkbox label="-1" @on-change="changeBreedType" v-model="allBreedType">
          <span>全选</span>
        </Checkbox>
        <CheckboxGroup v-model="obj.breedType" @on-change="getBreedType">
          <Checkbox label="1">
            <span>纯种</span>
          </Checkbox>
          <Checkbox label="2">
            <span>杂交</span>
          </Checkbox>
        </CheckboxGroup>
      </Col>
    </Row>
    <br />
    <Row>
      <Col span="4">性别</Col>
      <Col span="20" style="display:flex;">
        <Checkbox label="-1" v-model="allAnimalSex" @on-change="changeAnimalSex">
          <span>全选</span>
        </Checkbox>
        <CheckboxGroup v-model="obj.animalSex" @on-change="getAnimalSex">
          <Checkbox label="1">
            <span>公猪</span>
          </Checkbox>
          <Checkbox label="2">
            <span>阉公猪</span>
          </Checkbox>
          <Checkbox label="3">
            <span>母猪</span>
          </Checkbox>
        </CheckboxGroup>
      </Col>
    </Row>
    <br />
    <template v-for="(item) in typeList">
      <Row style="margin-bottom:50px;" v-if="item.indexLists">
        <div v-if="item.indexLists">
          <Col span="4">{{item.parentName?item.parentName + '-':''}}{{item.indexTypeName}}</Col>
          <Col span="20" style="display:flex;flex-wrap:wrap">
            <Checkbox label="-1" v-model="item.all" @on-change="changeIndexId(item)">
              <span>全选</span>
            </Checkbox>
            <CheckboxGroup
              v-for="(item1,index1) in item.indexLists"
              :key="index1"
              v-model="item.indexIdList"
              @on-change="getIndexId(item,item1.indexId)"
            >
              <Checkbox :label="item1.indexId" style="margin-right:30px;margin-bottom:10px;">
                <span>{{item1.indexName}}</span>
              </Checkbox>
            </CheckboxGroup>
          </Col>
        </div>
      </Row>
    </template>
    <br />
    <br />
    <Row>
      <Col span="4">搜索结果</Col>
      <Col span="20" style="text-align:right;">
        <Button type="info" @click="allLoad">全部下载</Button>
      </Col>
    </Row>
    <Row>
      <Table style="margin-top:20px;" :columns="columns" border :data="list"></Table>
    </Row>
    <Row>
      <Col span="24" style="margin-top:40px;margin-bottom:40px;text-align:center;">
        <Page :total="total" size="small" show-total @on-change="changePage" />
      </Col>
    </Row>
  </div>
</template>
<script>
export default {
  data() {
    let _this = this;
    return {
      index: 0,
      datetime: [],
      typeList: [],
      total: 0,
      obj: {
        page: 1,
        pageSize: 10,
        keyword: "",
        breedType: [],
        animalSex: [],
        startTime: "",
        endTime: "",
        indexIdList: []
      },
      allBreedType: false,
      allAnimalSex: false,
      indexId: false,
      list: [],
      columns: [
        {
          type: "index",
          title: "序号",
          key: "",
          align: "center",
          width: 100
        },
        {
          title: "参考文献",
          key: "referLiterature",
          align: "center",
          width: 300
        },
        {
          title: "猪种",
          key: "pigBreed",
          align: "center",
          width: 100
        },
        {
          title: "纯种或杂交等信息",
          key: "",
          align: "center",
          width: 100,
          render: function(h, param) {
            return h("div", {}, param.row.breedType == 1 ? "纯交" : "杂交");
          }
        },
        {
          title: "性别",
          key: "",
          align: "center",
          width: 80,
          render: function(h, param) {
            return h(
              "div",
              {},
              param.row.animalSex == 1
                ? "公猪"
                : param.row.animalSex == 2
                ? "阉公猪"
                : "母猪"
            );
          }
        },
        {
          title: "饲养周期",
          key: "feedPeriod",
          align: "center",
          width: 100
        },
        {
          title: "取样位置",
          key: "sampPoint",
          align: "center",
          width: 100
        },
        {
          title: "产地",
          key: "place",
          align: "center",
          width: 200
        },
        {
          title: "饲养信息",
          key: "feedInformation",
          align: "center",
          width: 200
        },
        {
          title: "屠宰方式",
          key: "butcherWay",
          align: "center",
          width: 200
        },
        {
          title: "冷却方式",
          key: "coolWay",
          align: "center",
          width: 200
        },
        {
          title: "贮藏方式",
          key: "storeCondition",
          align: "center",
          width: 200
        },
        {
          title: "运输条件",
          key: "transportationCondition",
          align: "center",
          width: 200
        },
        {
          fixed: "right",
          title: "操作",
          key: "",
          width: 200,
          align: "center",
          render: function(h, param) {
            return h("div", {}, [
              h(
                "a",
                {
                  style: {
                    color: "#00C1DE",
                    "margin-right": "10px"
                  },
                  on: {
                    click: () => {
                      _this.toDownLoad(param.row.literatureId);
                    }
                  }
                },
                "下载"
              )
            ]);
          }
        }
      ]
    };
  },
  methods: {
    toDownLoad(id) {
      window.location.href =
        "/api/literatureExcel/exportExcel?literatureId=" + id;
    },
    allLoad() {
      if (this.index < this.list.length) {
        window.location.href =
          "/api/literatureExcel/exportExcel?literatureId=" +
          this.list[this.index].literatureId;
        setTimeout(() => {
          this.index = this.index + 1;
          this.allLoad();
        }, 1000);
      }else{
        this.index = 0
        this.$Message.success('下载完成！')
      }
    },
    changePage(page) {
      this.obj.page = page;
    },
    changeBreedType(e) {
      if (e) {
        this.obj.breedType = ["1", "2"];
      } else {
        this.obj.breedType = [];
      }
    },
    getBreedType(e) {
      console.log("e", e);
      if (e.length == 2) {
        this.allBreedType = true;
      } else {
        this.allBreedType = false;
      }
      this.getData();
    },
    changeAnimalSex(e) {
      if (e) {
        this.obj.animalSex = ["1", "2", "3"];
      } else {
        this.obj.animalSex = [];
      }
      this.getData();
    },
    getAnimalSex(e) {
      if (e.length == 3) {
        this.allAnimalSex = true;
      } else {
        this.allAnimalSex = false;
      }
      this.getData();
    },
    changeIndexId(item1) {
      let list = this.typeList;
      list.forEach((item, index) => {
        if (item1.indexTypeId == item.indexTypeId) {
          if (item.all) {
            item.indexLists.forEach(item2 => {
              item.indexIdList.push(item2.indexId);
            });
            item.all = true;
            console.log("arr", list);
          } else {
            item.indexIdList.splice(0, item.indexIdList.length);
            item.all = false;
          }
          this.typeList = list;
          console.log("typeList", list);
          this.getData();
        }
      });
    },
    getIndexId(item1, indexId) {
      let list = this.typeList;
      list.forEach((item, index) => {
        if (item1.indexTypeId == item.indexTypeId) {
          let i = false;
          console.log("item.indexIdList", item.indexIdList);
          item.indexIdList.forEach((item2, index2) => {
            if (indexId == item2) {
              i = true;
              // item.indexIdList.splice(index2,1)
              // console.log(item.indexIdList)
            }
          });
          if (!i) {
            item.indexIdList.push(indexId);
          }
          if (item.indexIdList.length != item.indexLists.length) {
            item.all = false;
          } else {
            item.all = true;
          }
        }
      });
      this.typeList = list;
      console.log("this.typeList", this.typeList);
      this.getData();
    },
    getType() {
      this.$axios
        .post("/api/literature/getIndexTypeAndIndexList", {})
        .then(res => {
          if (res.data.code == 100) {
            this.typeList = res.data.data;
            this.typeList.forEach(item => {
              item.all = false;
              item.indexIdList = [];
            });
          }
        });
    },
    changeDate() {
      this.getData();
    },
    getData() {
      let form = new FormData();
      // obj= {
      //   page: 1,
      //   pageSize: 10,
      //   keyword: "",
      //   breedType: [],
      //   animalSex: [],
      //   startTime: "",
      //   endTime: "",
      //   indexIdList: []
      // }
      form.append("page", this.obj.page);
      form.append("pageSize", this.obj.pageSize);
      this.typeList.forEach(item => {
        item.indexIdList.forEach(item1 => {
          this.obj.indexIdList.push(item1);
        });
      });
      form.append("indexIdList", this.obj.indexIdList.join(","));
      form.append("animalSex", this.obj.animalSex.join(","));
      this.obj.keyword ? form.append("keyword", this.obj.keyword) : "";
      if (this.datetime[0] && this.datetime[1]) {
        form.append("startTime", this.datetime[0].valueOf());
        form.append("endTime", this.datetime[1].valueOf());
      }
      this.$axios
        .post("/api/literature/getLiteratureByFillPage", form)
        .then(res => {
          this.obj.indexIdList = [];
          this.list = res.data.data.records;
          this.total = res.data.data.total;
        });
    }
  },
  mounted() {
    this.getType();
    this.getData();
  }
};
</script>


